.container{
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(179, 179, 179);
    padding: 10px;
    &>div{
        display: flex;
        flex-direction: column;
    }
}
.number{
    font-size: small;
    font-weight: bold;
}
.currentDay{
    background-color: rgb(0, 102, 255);
    color: rgb(235, 229, 229);
}
.day{
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 5px;
}